<template>
  <div>
    <!-- 页面主体部分 -->
    <section class="mainbox">
      <div class="colunmn">
        <div class="panel bar">
          <h2>动态排序柱状图</h2>
          <BarRace></BarRace>
          <div class="panel-footer"></div>
        </div>
        <div class="panel line">
          <h2>水波图</h2>
          <liquidWave></liquidWave>
          <div class="panel-footer"></div>
        </div>
      </div>
      <div class="colunmn">
        <PlantWeather></PlantWeather>
      </div>
      <div class="colunmn">
        <div class="panel bar2">
          <h2>折柱混合图</h2>
          <MixedLineBar></MixedLineBar>
          <div class="chart"></div>
          <div class="panel-footer"></div>
        </div>
        <div class="panel line2">
          <h2>基础雷达图</h2>
          <RadarChart></RadarChart>
          <div class="panel-footer"></div>
        </div>
      </div>
    </section>
    <section class="foot">
      <div class="child" v-for="item in chartsOptions" :key="item.id">
        <PlantEnvPanel
          @initData="initData"
          :myOption="myOption"
          :name="item.title"
          :unit="item.unit"
          :max="item.max"
          :data="item.data"
          :leftScal="item.leftScal"
          :rightScal="item.rightScal">
          <template #title>
            {{ item.title }}
          </template>
          <template #icon>
            <img :src="item.icon" alt="" />
          </template>
        </PlantEnvPanel>
      </div>
    </section>
  </div>
</template>
<script>
import option from "@/assets/js/echartOptions/panelOption.js";
import BarRace from "@/components/echarts/BarRace.vue";
import MixedLineBar from "@/components/echarts/MixedLineBar.vue";
import liquidWave from "@/components/echarts/liquidWave.vue";
import RadarChart from "@/components/echarts/RadarChart.vue";
export default {
  data() {
    return {
      myOption: option,
      staticData: [
        {
          id: 1,
          name: "airTemp",
          title: "空气温度",
          icon: require("@/assets/images/icos/temp3.png"),
          unit: "℃",
        },
        {
          id: 2,
          name: "soilTemp",
          title: "土壤温度",
          icon: require("@/assets/images/icos/sm2.png"),
          unit: "℃",
        },
        {
          id: 3,
          name: "ltTens",
          title: "光照强度",
          icon: require("@/assets/images/icos/lt3.png"),
          unit: "lx",
        },
        {
          id: 4,
          name: "soilMoi",
          title: "土壤湿度",
          icon: require("@/assets/images/icos/st2.png"),
          unit: "%",
        },
        {
          id: 5,
          name: "airMois",
          title: "空气湿度",
          icon: require("@/assets/images/icos/am2.png"),
          unit: "%",
        },
        {
          id: 6,
          name: "CO2",
          title: "CO2浓度",
          icon: require("@/assets/images/icos/co2.png"),
          unit: "ppm",
        },
      ],
      dynamicData: [
        {
          max: 50,
          data: 30,
          leftScal: 20,
          rightScal: 30,
        },
        {
          max: 50,
          data: 25,
          leftScal: 20,
          rightScal: 30,
        },
        {
          max: 800,
          data: 300,
          leftScal: 200,
          rightScal: 600,
        },
        {
          max: 100,
          data: 60,
          leftScal: 50,
          rightScal: 70,
        },
        {
          max: 100,
          data: 30,
          leftScal: 50,
          rightScal: 60,
        },
        {
          max: 4000,
          data: 1500,
          leftScal: 2000,
          rightScal: 3000,
        },
      ],
      chartsOptions: [
        {
          name: "airTemp",
          id: 1,
          title: "空气温度",
          max: 50,
          data: 30,
          leftScal: 20,
          rightScal: 30,
          unit: "℃",
          icon: require("@/assets/images/icos/temp3.png"),
        },
        {
          name: "soilTemp",
          id: 2,
          title: "土壤温度",
          max: 50,
          data: 25,
          leftScal: 20,
          rightScal: 30,
          unit: "℃",
          icon: require("@/assets/images/icos/sm2.png"),
        },
        {
          name: "ltTens",
          id: 3,
          title: "光照强度",
          max: 800,
          data: 300,
          leftScal: 200,
          rightScal: 600,
          unit: "lx",
          icon: require("@/assets/images/icos/lt3.png"),
        },
        {
          name: "soilMoi",
          id: 4,
          title: "土壤湿度",
          max: 100,
          data: 60,
          leftScal: 50,
          rightScal: 70,
          unit: "%",
          icon: require("@/assets/images/icos/st2.png"),
        },
        {
          name: "airMois",
          id: 5,
          title: "空气湿度",
          max: 100,
          data: 30,
          leftScal: 50,
          rightScal: 60,
          unit: "%",
          icon: require("@/assets/images/icos/am2.png"),
        },
        {
          name: "CO2",
          id: 6,
          title: "CO2浓度",
          max: 4000,
          data: 1500,
          leftScal: 2000,
          rightScal: 3000,
          unit: "ppm",
          icon: require("@/assets/images/icos/co2.png"),
        },
      ],
    };
  },
  methods: {
    getRandomInt(min, max) {
      min = Math.ceil(min);
      max = Math.floor(max);
      return Math.floor(Math.random() * (max - min + 1)) + min;
    },
    initData(data) {
      this.myOption.series[2].data[0].value = data;
    },
  },
  created() {
    console.log(
      `window width: ${window.innerWidth}, window height: ${window.innerHeight}`
    );

    this.$nextTick(() => {
      let datas = [
        this.getRandomInt(0, 50),
        this.getRandomInt(0, 50),
        this.getRandomInt(0, 800),
        this.getRandomInt(0, 100),
        this.getRandomInt(0, 100),
        this.getRandomInt(0, 4000),
      ];
      this.chartsOptions.forEach((item, i) => {
        item.data = datas[i];
      });
    });
    setInterval(() => {
      let datas = [
        this.getRandomInt(0, 50),
        this.getRandomInt(0, 50),
        this.getRandomInt(0, 800),
        this.getRandomInt(0, 100),
        this.getRandomInt(0, 100),
        this.getRandomInt(0, 4000),
      ];
      this.chartsOptions.forEach((item, i) => {
        item.data = datas[i];
      });
    }, 2000);
  },
  components: {
    BarRace,
    MixedLineBar,
    liquidWave,
    RadarChart,
  },
};
</script>

<style scoped>
* {
  margin: 0;
  padding: 0;
  font-family: alimama;
}

.mainbox {
  display: flex;
  min-width: 64vw;
