  flex: 3;
}

.mainbox .colunmn:nth-child(1) {
  margin-left: 1.25vw;
}

.mainbox .colunmn:nth-child(3) {
  margin-right: 1.125vw;
}

.mainbox .colunmn:nth-child(2) {
  flex: 5;
  margin-left: 0.78125vw;
  margin-right: 0.78125vw;
  margin-bottom: 0.78125vw;
}

.panel {
  position: relative;
  height: 27vh;
  border: 0.0625vw solid rgba(25, 186, 139, 0.17);
  background: url(@/assets/images/linebg.png) rgba(255, 255, 255, 0.03);
  margin-bottom: 0.625vw;
}

.panel::before {
  position: absolute;
  top: 0;
  left: 0;
  width: 0.625vw;
  height: 0.625vw;
  border-top: 0.125vw solid #02a6b5;
  border-left: 0.125vw solid #02a6b5;
  content: "";
}

.panel::after {
  position: absolute;
  top: 0;
  right: 0;
  width: 0.625vw;
  height: 0.625vw;
  border-top: 0.125vw solid #02a6b5;
  border-right: 0.125vw solid #02a6b5;
  content: "";
}

.panel .panel-footer {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
}

.panel .panel-footer::before {
  position: absolute;
  left: 0;
  bottom: 0;
  height: 0.625vw;
  width: 0.625vw;
  border-left: 0.125vw solid #02a6b5;
  border-bottom: 0.125vw solid #02a6b5;
  content: "";
}

.panel .panel-footer::after {
  position: absolute;
  right: 0;
  bottom: 0;
  height: 0.625vw;
  width: 0.625vw;
  border-right: 0.125vw solid #02a6b5;
  border-bottom: 0.125vw solid #02a6b5;
  content: "";
}

.panel h2 {
  height: 4vh;
  color: #fff;
  line-height: 4vh;
  text-align: center;
  font-size: 16px;
  font-weight: 400;
  background: linear-gradient(to bottom, #0c252f80, #025feb32);
}

.panel .chart {
  height: 23vh;
}

.bar2 .chart video {
  width: 100%;
  height: 100%;
}

.msg-left {
  float: left;
  width: 13.125vw;
  height: 100%;
  border-right: 3px solid rgba(61, 113, 159, 0.216);
}

.msg-right {
  float: left;
  width: 13.125vw;
  height: 100%;
  border-left: 3px solid rgba(61, 113, 159, 0.216);
}

.msg-title {
  height: 24px;
  line-height: 24px;
  font-size: 16px;
  color: gainsboro;
  text-align: center;
}

.msg-title::before {
  content: "";
  display: inline;
  position: relative;
  top: 0.4375vw;
  height: 100%;
  width: calc(20%);
  border-bottom: 0.0625vw solid rgba(62, 106, 167, 0.315);
}

.msg-box {
  display: grid;
  grid-template-columns: 1fr 1fr; /* 两列，每列占据1个单位 */
  /* grid-template-rows: 1fr 1fr; 两行，每行占据1个单位 */
}

.msg-box ul li {
  height: 1.875vw;
  /* line-height: 30px; */
  position: relative;
  margin: 0.9375vw 0;
  margin-left: 0.9375vw;
  color: #fff;
  font-size: 16px;
  text-indent: 0.1875vw;
  margin-bottom: 0.3125vw;
  border-left: 0.125vw solid rgba(101, 132, 226, 0.1);
  border-bottom: 0.125vw solid rgba(101, 132, 226, 0.1);
}

.msg-box ul {
  height: 80%;
  overflow-y: auto;
  /*设置y轴可滑动 */
}

.msg-box ul::-webkit-scrollbar {
  display: none;
}

.msg-box ul li::before {
  content: "";
  position: relative;
  bottom: 0.9375vw;
  right: 0.625vw;
  width: 0;
  height: 0;
  border-left: 0.3125vw solid transparent;
  /* 左边框宽度等于三角形底边的一半 */
  border-right: 0.3125vw solid transparent;
  /* 右边框宽度等于三角形底边的一半 */
  border-bottom: 0.625vw solid rgba(182, 193, 226, 0.223);
  /* 下边框宽度等于三角形的高，颜色为三角形填充色 */
}

.show-detail {
  display: flex;
  height: 10vh;
  width: 10.95vw;
  /* background-image: ; */
  background: repeat-y url("@/assets/images/showbox.png");
  background-size: cover;
  background-position: center;
  border-radius: 18px;
  margin: 0.125rem auto;
}

.chart-detail {
  width: 100%;
  display: grid;
  grid-template-columns: 1fr 1fr; /* 两列，每列占据1个单位 */
  grid-template-rows: 1fr 1fr; /* 两行，每行占据1个单位 */
  /* margin: 0 auto; */
}

.show-detail span {
  font-size: 20px;
  color: white;
  font-weight: 10;
  display: block;
}

.show-detail span:nth-child(1) {
  line-height: 2.5vw;
