  font-family: alimama;
}

.show-detail span:nth-child(2) {
  line-height: 2.5vw;
  font-size: 1vw;
  height: 2.5vw;
  text-indent: 1.125vw;
  font-family: alimama;
}

.ico-contain {
  float: right;
  width: 2.125vw;
  height: 2.125vw;
  border-radius: 1.0625vw;
  background-color: #888888;
  outline: rgba(101, 166, 196, 0.445) solid thick;
  margin-top: 1.25vw;
  margin-left: 0.9375vw;
}

.ico-contain img {
  display: block;
  margin: 0.3125vw auto;
}

.show-detail div:nth-child(2) {
  float: right;
  width: 8.75vw;
}

.foot {
  display: flex;
  height: 30vh;
}

.foot .child {
  height: 20vh;
  flex: 6;
  margin: 0 1.25vw;
}

.child::before {
  position: absolute;
  top: 0;
  left: 0;
  width: 0.625vw;
  height: 0.625vw;
  border-top: 0.125vw solid #02a6b5;
  border-left: 0.125vw solid #02a6b5;
  content: "";
}

.child::after {
  position: absolute;
  top: 0;
  right: 0;
  width: 0.625vw;
  height: 0.625vw;
  border-top: 0.125vw solid #02a6b5;
  border-right: 0.125vw solid #02a6b5;
  content: "";
}
</style>
