<template>
  <div class="bar-race"></div>
</template>

<script>
export default {
  data() {
    return {
      data: [],
      option: {
        grid: {
          top: 10,
        },
        xAxis: {
          max: "dataMax",
        },
        yAxis: {
          type: "category",
          data: ["A", "B", "C", "D", "E"],
          inverse: true,
          animationDuration: 300,
          animationDurationUpdate: 300,
          max: 3, // only the largest 3 bars will be displayed
        },
        series: [
          {
            realtimeSort: true,
            name: "X",
            type: "bar",
            data: this.data,
            label: {
              show: true,
              position: "right",
              valueAnimation: true,
            },
          },
        ],
        legend: {
          show: false,
        },
        animationDuration: 0,
        animationDurationUpdate: 3000,
        animationEasing: "linear",
        animationEasingUpdate: "linear",
      },
      myChart: null,
    };
  },
  methods: {
    run() {
      for (var i = 0; i < this.data.length; i++) {
        if (Math.random() > 0.9) {
          this.data[i] += Math.round(Math.random() * 2000);
        } else {
          this.data[i] += Math.round(Math.random() * 200);
        }
      }
      this.myChart.setOption({
        series: [
          {
            type: "bar",
            data: this.data,
          },
        ],
      });
    },
  },
  mounted() {
    this.myChart = this.$echarts.init(document.querySelector(".bar-race"));
    setTimeout(() => {
      this.run();
    }, 0);
    setInterval(() => {
      this.run();
    }, 3000);
    this.option && this.myChart.setOption(this.option);
  },
  created() {
    for (let i = 0; i < 5; ++i) {
      this.data.push(Math.round(Math.random() * 200));
    }
  },
};
</script>

<style scoped>
.bar-race {
  width: 100%;
  height: 100%;
}
</style>
