<template>
  <div class="liquid-wave"></div>
</template>

<script>
export default {
  data() {
    return {
      data: [],
      option: {
        title: {
          top: "47%",
          left: "center",
          text: 80 + " 分",
          textStyle: {
            color: "#fff",
            fontStyle: "normal",
            fontWeight: "normal",
            fontSize: 32,
          },
        },
        series: [
          {
            type: "liquidFill",
            itemStyle: {
              opacity: 0.8, //波浪的透明度
              shadowBlur: 10, //波浪的阴影范围
              shadowColor: "#FFB931", //阴影颜色
            },
            radius: "80%",
            //水波
            color: [
              new this.$echarts.graphic.LinearGradient(0, 0, 0, 1, [
                {
                  offset: 0,
                  color: "#2e9dc5",
                },
                {
                  offset: 1,
                  color: "#126f8d",
                },
              ]),
            ],
            data: [
              {
                value: 0.8,
              },
            ],
            // background: '#000',
            center: ["50%", "50%"],
            backgroundStyle: {
              color: "#fff",
            },
            label: {
              normal: {
                formatter: "",
                textStyle: {
                  fontSize: 12,
                },
              },
            },
            outline: {
              itemStyle: {
                borderColor: "transparent",
                borderWidth: 5,
              },
              borderDistance: 0,
            },
          },
          //外环线
          {
            color: ["#0a5ca8", "transparent"],
            type: "pie",
            center: ["50%", "50%"],
            radius: ["80%", "82%"],
            hoverAnimation: false,
            data: [
              {
                name: "",
                value: 400,
                label: {
                  show: false,
                  position: "center",
                  color: "#fff",
                  fontSize: 38,
                  fontWeight: "bold",
                  formatter: function () {
                    return 400;
                  },
                },
              },
              {
                //画剩余的刻度圆环
                name: "",
                value: 100,
                label: {
                  show: false,
                },
                labelLine: {
                  show: false,
                },
              },
            ],
          },
        ],
      },
      myChart: null,
    };
  },
  methods: {},
  mounted() {
    this.myChart = this.$echarts.init(document.querySelector(".liquid-wave"));
    this.option && this.myChart.setOption(this.option);
  },
  created() {},
};
</script>

<style scoped>
.liquid-wave {
  width: 100%;
  height: 80%;
}
</style>
